<template>
  <div class="main-content">
    <div class="left">
      <HomeMenu
        @set-tab="
          (n) => {
            previousTab = activeTab;
            activeTab = n;
            posY = -20 * n;
          }
        "
        :activeTab="activeTab"
      />
    </div>
    <div class="middle">
      <TabContent :activeTab="activeTab" :previousTab="previousTab" />
      <div class="pop-up">
        <p>
          SCROLLING WILL TAKE YOU TO
          <span class="dest">
            <p class="text" :style="`transform: translateY(${computeOffsetY})`">
              <b>INFO</b>
            </p>
            <p class="text" :style="`transform: translateY(${computeOffsetY})`">
              <b>WORK</b>
            </p>
            <p class="text" :style="`transform: translateY(${computeOffsetY})`">
              <b>TOS</b>
            </p>
          </span>
        </p>
      </div>
    </div>
    <div class="right">
      <HomePortfolio />
    </div>
  </div>
</template>

<script>
import HomeMenu from "./homeMenu.vue";
import TabContent from "./tabContent.vue";
import HomePortfolio from "./HomePortfolio.vue";

export default {
  name: "HomeContent",
  components: {
    TabContent,
    HomeMenu,
    HomePortfolio,
  },
  data() {
    return {
      activeTab: 0,
      previousTab: 0,
      posY: 0,
    };
  },
  computed: {
    computeOffsetY() {
      return this.posY + "px";
    },
  },
  methods: {},
};
</script>

<style scoped>
.main-content {
  position: fixed;
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: 35% 30% 35%;
  justify-content: center;
  padding-top: 17%;
  transition: all 0.5s ease;
}

.left {
  justify-self: left;
  padding-left: 50%;
}

.right {
  justify-self: left;
  padding-left: 80px;
  padding-top: 60px;
}

.pop-up {
  margin: auto;
  color: white;
  font-size: 12px;
  margin-top: 120px;
}

.pop-up p {
  margin: 0 0 5px 0;
}

.dest {
  display: inline-flex;
  flex-direction: column;
  overflow: hidden;
  max-height: 19px;
  align-items: flex-start;
  margin-left: 2px;
  transition: all 0.2s ease;
}

.text {
  transition: transform 0.5s ease;
}

@media only screen and (max-width: 1400px) {
  .main-content {
    grid-template-columns: 30% 40% 30%;
  }
  .left {
    padding-left: 20%;
  }
}

@media only screen and (max-width: 1000px) {
  .main-content {
    grid-template-columns: 70%;
  }
  .left {
    display: none;
  }
  .right {
    display: none;
  }
}
</style>