<template>
  <div class="nav">
    <div class="left hoverable">
      <router-link to="/"><a class="home" @mouseover="isHover(true, 'home')" @mouseleave="isHover(false, 'home')">MAX </a>Â©2022</router-link>
    </div>
    <div class="right" id="nav-right">
        <router-link to="/404"><div class="button-container" @mouseover="isHover(true, 'work')" @mouseleave="isHover(false, 'work')">
          <a
          :style="computedTopWork"
          href="/" 
          class="nav-button">WORK WITH ME</a>
          <a href="/" :style="computedTopWork" class="nav-button">COMMISSIONS</a>
        </div></router-link>
        <router-link to="/404"><div class="button-container" @mouseover="isHover(true, 'tos')" @mouseleave="isHover(false, 'tos')">
          <a :style="computedTopTos" href="/" class="nav-button">TOS</a>
          <a :style="computedTopTos" href="/" class="nav-button">READ</a>
        </div></router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "NavigationBar",
  data() {
    return {
      topWork: 0,
      topTos: 0,
    }
  },
  computed: {
    computedTopWork() {
      return `transform: translateY(${this.topWork}%)`
    },
    computedTopTos() {
      return `transform: translateY(${this.topTos}%)`
    }
  },
  methods: {
    isHover(bool, el) {
        this.$root.$refs.cursor.expandCursor(bool);
        this.changeText(bool, el)
      },
      changeText(bool, el) {
        if (bool) {
          if (el == 'work') {
            this.topWork = -100;
            console.log(this.computedTopWork)
          } else if (el == 'tos') {
            this.topTos = -100;
          }
        } else {
          if (el == 'work') {
            this.topWork = 0;
          } else if (el == 'tos') {
            this.topTos = 0;
          }
        }
      }
  }
};
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css?family=Montserrat:100,200,300,400,500,600,700,800,900');

a,
a:active,
