  color: white;
}

.button-container {
  display: flex;
  flex-direction: column;
  margin-left: 40px;
  overflow: hidden;
  max-height: 19px;
}

.nav {
  font-family: Montserrat, Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 1px;
  z-index: 5;
  color: #f7f8fa;
}

.nav .left {
    color: black;
    position: fixed;
    top: 60px;
    left: 60px;
}

.nav a {
  /* color: black; */
  text-decoration: none;
}

.nav a:visited {
  color: #f7f8fa;
}

.nav .right {
  align-items: right;
  display: flex;
  position: fixed;
  top: 60px;
  right: 60px;
}

.home {
  font-weight: 500;
}

.nav .nav-button {
  font-weight: 500;
  position: relative;
  transition: transform 0.5s ease;
}

.nav .nav-button:hover {
  cursor: none;
}
</style>