<template>
    <div class="menu" @mouseover="isHover(true)" @mouseleave="isHover(false)">
        <div class="menu-button">
            <ReactiveSVG :activeTab="activeTab == 0" />
            <div @click="setTab(0)" class="text" :class="(activeTab == 0) ? 'active' : ''">
                <p>INFO</p>
            </div>
        </div>
        <div class="menu-button">
            <ReactiveSVG :activeTab="activeTab == 1" />
            <div @click="setTab(1)" class="text" :class="(activeTab == 1) ? 'active' : ''">
                <p>WORK</p>
            </div>
        </div>
        <div class="menu-button">
            <ReactiveSVG :activeTab="activeTab == 2" />
            <div @click="setTab(2)" class="text" :class="(activeTab == 2) ? 'active' : ''">
                <p>TOS</p>
            </div>
        </div>
    </div>
</template>

<script>
import ReactiveSVG from './reactiveSVG.vue';
export default {
    name: 'HomeMenu',
    components: { 
        ReactiveSVG 
    },
    props: [
        'activeTab'
    ],
    methods: {
        isHover(bool) {
            this.$root.$refs.cursor.expandCursor(bool);
        },
        setTab(int) {
            this.$emit('set-tab', int)
        }
    }
}
</script>

<style scoped>
    p {
        color: rgb(163, 163, 163);
        font-size: 28px;
        font-weight: 600;
        margin: 0 0 0 15px;
        text-align: left;
        transition: font-size 0.3s ease;
    }

    .active p {
        font-size: 42px;
        color: white;
    }

    .menu-button {
        display: flex;
        height: 50px;
        align-items: center;
        margin: 0 0 15px 0;
    }
</style>