<template>
    <div class="circles" ref="circles">
    </div>
</template>

<script>
import * as THREE from 'three'

const SEPARATION = 1000,
    AMOUNTX = 1,
    AMOUNTY = 1;

let container;
let camera, scene, renderer;

let particles, count = 0;

let mouseX = 0,
    mouseY = 0;

export default {
    name: 'ThreeCircles',
    data() {
        return {
            /* SEPARATION: SEPARATION,
            AMOUNTX: AMOUNTX,
            AMOUNTY: AMOUNTY,
            container: container,
            camera: camera,
            scene: scene,
            renderer: renderer,
            particles: particles,
            count: count,
            mouseX: mouseX,
            mouseY: mouseY, */
            windowHeight: 0,
            windowWidth: 0,
            windowHalfX: 0,
            windowHalfY: 0
        }
    },
    methods: {
        init: function () {
            container = document.createElement('div');
            this.$refs.circles.appendChild(container);

            camera = new THREE.PerspectiveCamera(120, this.windowWidth / this.windowHeight, 0.0001, 1000000);
            camera.position.z = 100;
            camera.position.x = 0;
            camera.position.y = 0;

            scene = new THREE.Scene();

            const numParticles = AMOUNTX * AMOUNTY;
            const positions = new Float32Array(numParticles * 3);
            const scales = new Float32Array(numParticles);

            let i = 0,
                j = 0;

            for (let ix = 0; ix < AMOUNTX; ix++) {

                for (let iy = 0; iy < AMOUNTY; iy++) {

                    positions[i] = ix * SEPARATION - ((AMOUNTX * SEPARATION) / 3); // x
                    positions[i + 1] = 0; // y
                    positions[i + 2] = iy * SEPARATION - ((AMOUNTY * SEPARATION) / 2); // z

                    scales[j] = 2;

                    i += 3;
                    j++;

                }

            }

            const geometry = new THREE.BufferGeometry();
            geometry.setAttribute('position', new THREE.BufferAttribute(positions, 3));
            geometry.setAttribute('scale', new THREE.BufferAttribute(scales, 1));

            const material = new THREE.ShaderMaterial({

                uniforms: {
                    color: {
                        value: new THREE.Color(0xffffff)
                    },
                },
                vertexShader: `attribute float scale;

                    void main() {

                    vec4 mvPosition = modelViewMatrix * vec4( position, 1.0 );

                    gl_PointSize = scale * ( 300.0 / - mvPosition.z );

                    gl_Position = projectionMatrix * mvPosition;

                    }`,
                fragmentShader: `uniform vec3 color;

                    void main() {

                    if ( length( gl_PointCoord - vec2( 0.5, 0.5 ) ) > 0.475 ) discard;

                    gl_FragColor = vec4( color, 1.0 );

                    }`

            });

            particles = new THREE.Points(geometry, material);
            scene.add(particles);

            renderer = new THREE.WebGLRenderer({
                antialias: true
            });
            renderer.setPixelRatio(window.devicePixelRatio);
            renderer.setSize(window.innerWidth, window.innerHeight);
            container.appendChild(renderer.domElement);

            container.style.touchAction = 'none';
            window.addEventListener('pointermove', this.onPointerMove);
            window.addEventListener('resize', this.onWindowResize);
        },
        onWindowResize() {
            camera.aspect = this.windowWidth / this.windowHeight * 2;
            camera.updateProjectionMatrix();

            renderer.setSize(this.windowWidth, this.windowHeight);
        },
        onPointerMove(event) {
            if (event.isPrimary === false) return;

            mouseX = (event.clientX - this.windowHalfX) / 50;
            mouseY = (event.clientY - this.windowHalfY) / 20;
        },
        animate: function () {
            if (scene && camera) {
                requestAnimationFrame(this.animate);
                this.render();
            }
        }, 
        render: function() {
            camera.position.x += (mouseX - camera.position.x) * 0.005;
            camera.position.y += (-mouseY - camera.position.y) * 0.005;
            camera.lookAt(scene.position);

            const positions = particles.geometry.attributes.position.array;
            const scales = particles.geometry.attributes.scale.array;

            let i = 0, j = 0;

            for (let ix = 0; ix < AMOUNTX; ix++) {

                for (let iy = 0; iy < AMOUNTY; iy++) {

                    positions[i + 1] = (Math.sin((ix + count) * 0.3) * 260) +
                        (Math.sin((iy + count) * 0.5) * 20);

                    scales[j] = (Math.sin((ix + count) * 0.2) + 1) * 60 +
                        (Math.sin((iy + count) * 0.1) + 1) * 320 + (Math.cos((iy + count / 2) * 0.2) * 50);

                    i += 3;
                    j++;

                }

            }

            particles.geometry.attributes.position.needsUpdate = true;
            particles.geometry.attributes.scale.needsUpdate = true;

            renderer.render(scene, camera);

            count += 0.02;

        }
    },
    beforeMount() {
        this.windowHeight = window.innerHeight;
        this.windowWidth = window.innerWidth;
        this.windowHalfX = this.windowWidth/ 2;
        this.windowHalfY = this.windowHeight / 2;
    },
    mounted() {
        this.init()
        this.animate()
    },
    beforeUnmount() {
        this.$refs.circles.removeChild(container);
        container = null;
        camera = null; 
        scene = null; 
        renderer = null;

        particles = 0;
        count = 0;

        mouseX = 0,
        mouseY = 0;
    }
}
</script>

<style scoped>
    .circles {
        display: block;
        position: absolute;
        width: 100%;
        mix-blend-mode: difference;
        overflow: hidden;
    }
</style>