<template>
  <div class="wrapper">
    <CircleCue
      class="cue"
      :style="{
        top: windowHeight / 2 - 30 + 'px',
        'margin-left': windowWidth * 0.18 + 'px',
      }"
      :class="(active == 'right') ? 'active' : 'hidden'"
      v-if="$root.isAnimated"
    />
    <ThreeCircles v-if="$root.isAnimated" :style="{ opacity: circlesOpacity, 'transition': 'all 2.5s ease' }" />
    <home-content class="blend" :style="{ transform: 'translateY(-' + computedOffset}" />
    <!-- <home-content :style="{top: windowHeight + 'px', transform: 'translateY(-' + computedOffset }"/> -->
  </div>
</template>

<script>
import ThreeCircles from "../../components/threeCircles.vue";
import CircleCue from "../../components/circleCue.vue";
import HomeContent from './components/homeContent.vue';
import debounce from 'lodash/debounce';

export default {
  name: "HomeView",
  components: {
    ThreeCircles,
    CircleCue,
    HomeContent,
  },
  data() {
    return {
      windowWidth: 0,
      windowHeight: 0,
      posY: 0,
      timer: -1,
      circlesOpacity: 0,
    };
  },
  methods: {
    handleScroll() {
      // Any code to be executed when the window is scrolled
      if (this.timer != -1) clearTimeout(this.timer);
      console.log(window.scrollY);
      let x = window.scrollY / this.windowHeight * 100;
      let y = -0.07069832 + 1.281517 * x - 0.1068963 * x**2 + 0.00399575 * x**3 - 0.00004924661 * x**4 + 1.969865 / 10**7 * x**5
      this.posY = this.windowHeight * y / 100

      this.timer = window.setTimeout( () => { this.resolvePosition() }, 1000);
      
      if (this.posY > 0.95 * this.windowHeight) {
        window.setTimeout( () => { this.$router.push('/404') }, 300);
      }
    },
    resolvePosition() {
      if (this.posY > 0.5 * this.windowHeight) {
        this.posY = this.windowHeight
        window.scrollTo(0, this.windowHeight);
      } else {
        this.posY = 0;
        window.scrollTo(0, 0);
      }
    }
  },
  computed: {
    computedOffset() {
      return this.posY + 'px'
    }
  },
  beforeMount() {
    this.windowHeight = window.innerHeight;
    this.windowWidth = window.innerWidth;
  },
  mounted() {
    setTimeout(() => {
      this.circlesOpacity = 1;
    }, 1000);
    this.handleDebouncedScroll = debounce(this.handleScroll, 5)
    window.addEventListener('scroll', this.handleDebouncedScroll);

    window.addEventListener("resize", () => {
      this.windowHeight = window.innerHeight;
      this.windowWidth = window.innerWidth;
    });
  },
  beforeUnmount() {
    window.removeEventListener('scroll', this.handleDebouncedScroll);
  }
};
</script>

<style scoped>
.active {
  opacity: 1;
  transition: opacity 0.5s ease, margin-left 0.5s ease;
}

.hidden {
  opacity: 0;
  transition: opacity 0.5s ease, margin-left 0.5s ease;
}

.active-transparent {
  opacity: 0.2;
  transition: opacity 0.5s ease, margin-left 0.5s ease;
}

.blend {
  mix-blend-mode: difference;
}

.cue {
  position: absolute;
}

.cue2 {
  position: absolute;
  transform: scale(3);
  transform-origin: center;
}

.wrapper {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  position: absolute;
  top: 0;
  width: 100%;
  height: 200vh;
  transition: transform 0.5s ease;
}

.wrapper p {
  margin: 0;
}
</style>