<template>
    <div class="tab-wrap">
        <div class="tab-content">
            <transition-group :name="(activeTab < previousTab) ? 'slideback' : 'slide'">
                <div v-if="activeTab == 0" class="info">
                    <p>Hi! My name is Max, although I usually go by 
                        <span class="button-container" 
                        @mouseenter="toggleSigma(true)"
                        @mouseleave="toggleSigma(false)"
                        :style="!isSigma ? {'margin-right': '-18px'} : {'margin-right': '0px'}">
                            <a :class="isSigma ? 'sigma' : ''">'Sigma'</a>
                            <a :class="isSigma ? 'sigma' : ''">'Shrigma'</a>
                        </span> 
                        online.
                    </p>
                    <p>
                        I specialize in graphic and motion design, but I also like to believe
                        that I can code some cool things sometimes. I'm pretty sure you came here 
                        to get some information about my commission process and prices, but while you are
                        at it, take a look around the website, you might find some fun and interesting
                        features!
                    </p>
                    <p> My portfolio is a work in progress though, please check back later this month!</p>
                </div>
                <div v-if="activeTab == 1" class="work">
                    <p>
                        Interested in working with me? Cool! 
                        Here's a short version of my prices:
                    </p>
                    <li>
                        Any static fullscreen graphic: <b>110 USD</b>
                    </li>
                    <li>
                        Any graphic can be animated for additional <b>30 USD</b>
                    </li>
                    <li>
                        Stinger: <b>150 USD</b>
                    </li>
                    <li>
                        Coding starts at <b>100 USD</b> depending on the element
                    </li>
                    <p>
                        Please, email your request to 
                        <a @mouseover="isHover(true)" 
                        @mouseleave="isHover(false)" 
                        href="mailto:admin@mccw.studio">
                            admin@mccw.studio
                        </a>. 
                        Note that my commissions are currently <b>selective</b>
                        due to very busy schedule. Thank you for the interest!
                    </p>
                </div>
                <div v-if="activeTab == 2" class="tos">
                    <p> Here's the short version:</p>
                    <li>
                        If I do not think that this commission is up my alley, I have the right to refuse.
                    </li> 
                    <li>
                        I own the rights to my own work, and as such might use it for promotion.
                    </li> 
                    <li>
                        The work I create is served under a semi-commercial license, 
                        meaning that you can profit off of it indirectly (for example using it for livestreams, Twitter posts, etc), 
                        but cannot directly sell it as, for example, merch. 
                        If you are unsure of whether your use constitutes commercial use, or you want to arrange commercial use, contact me.
                    </li>                        
                </div>
            </transition-group>
        </div>
        <router-link :to="computedActiveLink" class="button" >
            <p @mouseover="isHover(true)"
          @mouseleave="isHover(false)"
            >CONTINUE READING</p>
            <b-icon icon="arrow-down-circle-fill"
            @mouseover="isHover(true)"
          @mouseleave="isHover(false)"
            ></b-icon>
        </router-link>
    </div>
</template>

<script>
export default {
    name: 'TabContent',
    components: {
    },
    props: [
        'activeTab',
        'previousTab'
    ],
    data() {
        return {
            isSigma: false,
            activeLink: '/about',
        }
    },
    computed: {
        computedActiveLink() {
            this.checkActiveLink()
            return this.activeLink
        }
    },
    methods: {
        toggleSigma(bool) {
            this.isSigma = bool
        },
        checkActiveLink() {
            if (this.activeTab == 0) {
                this.activeLink = '/404'
            }
            else if (this.activeTab == 1) {
                this.activeLink = '/404'
            }
            else if (this.activeTab == 2) {
                this.activeLink = '/404'
            }
        },
        isHover(bool) {
            this.$root.$refs.cursor.expandCursor(bool);
        }
    }
}
</script>

<style scoped>
    .work a,
    .work a:visited,
    .work a:hover,
    .work a:active {
        color: yellow;
        font-weight: 600;
    }

    .tab-wrap {
        display: flex;
        flex-direction: column;
    }

    .tab-content {
        color: white;
        position: relative;
        overflow: hidden;
        letter-spacing: 0.1px;
        line-height: 20px;
        font-size: 16px;
        height: 320px;
    }

    .sigma {
        transform: translateY(-20px);
        transition: transform 0.5s ease;
    }

    .button-container {
        display: inline-flex;
        flex-direction: column;
        overflow: hidden;
        max-height: 19px;
        transition: all 0.2s ease;
    }
    
    .button-container a {
        position: relative;
        transition: all 0.5s ease;
    }

    .info-wrap {
        display: flex;
    }

    .button {
        color: white;
        font-size: 12px;
    }

    .button svg {
        font-size: 3em;
        animation: idle 2s ease-in-out infinite;
    }

    .info, .work, .tos {
        position: absolute;
        text-align: justify;
    }

    .slide-leave-active,
    .slide-enter-active {
        transition: 0.5s;
    }

    .slide-enter-from {
        transform: translate(0, 150%);
    }

    .slide-leave-to {
        transform: translate(0, -150%);
    }

    .slideback-leave-active,
    .slideback-enter-active {
        transition: 0.5s;
    }

    .slideback-enter-from {
        transform: translate(0, -150%);
    }

    .slideback-leave-to {
        transform: translate(0, 150%);
    }

    @keyframes idle {
        0% {transform: translateY(5px);}
        50% {transform: translateY(-5px);}
        100% {transform: translateY(5px);}
    }

</style>